@import "reset.scss";
$main-color-blue: #001DE1;
$main-color-yellow: #fff969;

@font-face {
    font-family: 'Exo 2.0 Regular';
    src: local('Exo 2.0 Regular');
    src: url(../fonts/Exo20-Regular.eot);
    src: url('../fonts/Exo20-Regular.ttf') format('embedded-opentype'),
    url('../fonts/Exo20-Regular.woff') format('woff'),
    url('../fonts/Exo20-Regular.ttf') format('truetype') {}
    font-weight: Regular;
    font-style: normal; }
@font-face {
    font-family: 'Exo 2.0 SemiBoldItalic';
    src: local('Exo 2.0 SemiBoldItalic');
    src: url(../fonts/Exo20-SemiBoldItalic.eot);
    src: url('../fonts/Exo20-SemiBoldItalic.ttf') format('embedded-opentype'),
    url('../fonts/Exo20-SemiBoldItalic.woff') format('woff'),
    url('../fonts/Exo20-SemiBoldItalic.ttf') format('truetype') {}
    font-weight: SemiBoldItalic;
    font-style: normal; }
@font-face {
    font-family: 'Impact';
    src: local('Impact');
    src: url(../fonts/Impact.eot);
    src: url('../fonts/Impact.ttf') format('embedded-opentype'),
    url('../fonts/Impact.woff') format('woff'),
    url('../fonts/Impact.ttf') format('truetype') {}
    font-weight: impact;
    font-style: normal; }
html {
    font-family: 'Exo 2.0 Regular';
    scroll-behavior: smooth; }
* {
    box-sizing: border-box; }
body {
    background: #feffe1; }
.section {
    width: 1160px;
    margin: 0 auto; }

.first-section {
