        background: $main-color-yellow;
        padding: 10px 0 5px 0; }
    &__nav-container {
        display: flex;
        justify-content: space-between;
        height: 50.68px;
        align-items: center;
        margin-bottom: 8.32px; }
    &__logo {
 }        // margin-right: 54.34px
    &__slider-part {
        height: 550px;
        background: #A9D0F1; }
    &__slider {
        max-width: 1440px;
        height: 550px;
        background: url(../img/firstphoto.png) no-repeat center; } }
.nav-menu {
