        color: $main-color-blue;
        &:last-child {
            margin-right: 0px; }
        &_number {
            display: flex;
            justify-content: flex-end;
            margin-right: 20px; } } }
a {
    text-decoration: none;
    color: #001DE1; }
.subtext {
