        padding-left: 60px; }
    &__img {
        margin-right: 13px; } }
.img_third {
    padding-top: 11px; }

.add-text {
    border: 1px solid rgba(0, 0, 0, 0.25);
    width: 482px;
    font-size: 16px;
    margin-right: 0.5px; }
.second-section {
    min-height: 890px;
    margin-bottom: 60px;
    &__heading {
        margin-bottom: 25px; }
    &__subtext {
        margin-bottom: 50px; } }
.heading {
    font-family: 'Impact';
    font-weight: impact;
    font-size: 45px;
    color: $main-color-blue;
    text-align: center;
    padding-bottom: 7px;
    position: relative;
    &__hr {
        width: 231px;
        position: absolute;
        bottom: 0px;
        margin: 0px;
        left: 463px;
        border: none;
        border: 1.4px solid #6aa6ff; } }
.heading-subtext {
    font-family: 'Exo 2.0 SemiBoldItalic';
    font-weight: SemiBoldItalic;
    text-align: center;
    font-size: 20px;
    letter-spacing: 0.08em; }
