    &__second-photo {
        max-width: 187px;
        max-height: 192px;
        top: 36.3px;
        right: 31.47px; }
    &__third-photo {
        max-width: 144px;
        max-height: 134px;
        top: 75.3px;
        right: 52.47px; }
    &__fourth-photo {
        max-width: 192px;
        max-height: 144px;
        top: 42.3px;
        right: 33.47px; }
    &__fifth-photo {
        max-width: 240px;
        top: 20.299999999999997px;
        right: -0.5300000000000011px; }
    &__sixth-photo {
