        justify-content: center;
        align-items: center;
        font-size: 15.75px; }
    &__text {
        color: black;
        margin: 15.5px; }
    &__arrow {
        stroke-width: 1.17px;
        stroke: #001de1; } }
.third-section {
    position: relative;
    margin-bottom: 60px;
    &__heading {
        margin-bottom: 25px; }
    &__subtext {
        margin-bottom: 150px; }
    &__img {
        position: absolute;
        top: 47px;
        left: 161px;
        max-height: 181px; } }
.regular-text {
    max-width: 813px;
    text-align: center;
    line-height: 150%;
    letter-spacing: 0.08em;
    color: rgba(0, 0, 0, 0.8); }

.fourth-section {
    &__heading {
        margin-bottom: 25px; }
    &__subtext {
        margin-bottom: 50px; }
    &__subtext-two {
