    &__subsection-text-healing {
        font-size: 18px;
        letter-spacing: 0.08em;
        text-align: center;
        color: #0500ff; }
    &__subsection-first-input {
        margin-bottom: 21.1px;
        position: relative;
        width: 307px; }
    &__subsection-second-input {
        margin-bottom: 26.6px;
        position: relative;
        width: 307px; }
    &__subsection-third-input {
        width: 307px; }
    &__buttons-form-text {
        font-weight: 400;
        font-size: 13.21px;
        line-height: 0%;
        letter-spacing: 0.15em;
        text-align: center;
        color: #001de1; }
    &__icon-container {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0; }
    &__icon-user {
        padding-left: 8px;
        font-size: 13.21px; }
    &__icon-call {
        padding-left: 8px; } }
.form-name {
    outline: 0;
    border: 1.75px solid #ddd;
    border-radius: 26px;
    font-size: 13.21px;
    width: 100%;
    height: 39px;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.1);
    background: #fff;
    padding: 10px;
    padding-left: 32px;
    &:focus {
        color: $main-color-blue;
        border-color: $main-color-yellow; } }
#phone-mask {
    outline: 0;
    border: 1.75px solid #ddd;
    border-radius: 26px;
    width: 100%;
    height: 39px;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.1);
    background: #fff;
    padding: 10px;
    padding-left: 32px;
    &:focus {
        color: $main-color-blue;
        border-color: $main-color-yellow; } }
#form-textarea {
    outline: 0;
    border: 1.75px solid #ddd;
    font-size: 13.21px;
    border-radius: 26px;
    width: 100%;
    height: 110px;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.1);
    background: #fff;
    margin-bottom: 25.18px;
    resize: none;
    padding: 10px;
    padding-top: 15px;
    &:focus {
        color: $main-color-blue;
        border-color: $main-color-yellow; } }
.buttons-form {
    width: 182px;
    height: 48px;
    margin: 0 auto;
    border: none;
    // border: 2px solid rgba(0, 0, 0, 0.48)
    border-radius: 19px;
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.25);
    background: #fffa7c; }
.social-media {
