        margin-bottom: 37.5px;
        font-size: 22.5px; }
    &__second-subheadings-list {
        display: grid;
        grid-template: repeat(3, auto) / repeat(2, auto);
        column-gap: 30px; }
    &__hr {
        width: 814px;
        position: absolute;
