        border: 1px solid $main-color-blue;
        height: 0px;
        top: 70px;
        left: 15px; }
    &__subheadings {
        display: flex;
        justify-content: space-around;
        flex-direction: row;
        text-align: center; }
    &__first-list-item {
        font-size: 16.75px;
        &:not(:last-child) {
            margin-bottom: 11px; } }
    &__second-list-item {
        font-size: 16.75px;
        &:not(:last-child) {
            margin-bottom: 11px; } }
    &__third-list-item {
        font-size: 16.75px;
        &:not(:last-child) {
            margin-bottom: 11px; } } }
